/* General Container Styling */
.container {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

/* Navigation Menu Styling */
nav ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  gap: 20px; /* Add proper spacing between items */
}

nav ul li {
  margin: 0;
}

nav ul li a, nav ul li button {
  text-decoration: none;
  color: #007bff;
  font-weight: bold;
  padding: 10px 20px;
  background-color: #f0f0f0;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  border: none;
  cursor: pointer;
}

nav ul li a:hover, nav ul li button:hover {
  background-color: #007bff;
  color: white;
}

/* Adjust Header Styling */
h1, h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 2rem;
  color: #007bff;
}

/* Admin Tools Section */
.admin-tools {
  margin-top: 30px; /* Add spacing to separate from navigation */
  text-align: center;
}

.admin-tools button {
  margin: 10px;
  padding: 12px 24px;
  font-size: 1.2rem;
  color: white;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.admin-tools button:hover {
  background-color: #0056b3;
}

/* Fix overlap issues */
nav {
  margin-bottom: 30px; /* Space below navigation */
}

/* Text Content Styling */
p {
  font-size: 1.2rem;
  color: #555;
  text-align: justify;
  margin-bottom: 20px;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
  nav ul {
    flex-direction: column; /* Stack menu items vertically on small screens */
    gap: 15px;
  }

  nav ul li a, nav ul li button {
    padding: 10px 15px; /* Adjust padding for smaller devices */
  }

  h1, h2 {
    font-size: 1.8rem;
  }

  p {
    font-size: 1rem;
  }
}
